:root {
  font-size: 62.5%;

  --tarefasFazerBackground: #FFF384;
  --headerTarefasFeitas: #FFC700;

  --tarefasFeitasBackground: #96E594;
  --headerTarefasFeitas: #77C06B;
}

/* TAREFAS CONFIGURAÇÕES GLOBAIS */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  width: 70%;
  margin-inline: auto;
  font-family: Arial, Helvetica, sans-serif;
  font-family: 1.6rem;
  background-color: rgb(138, 181, 194);
  margin-top: 2rem;
}

ul {
  list-style: none;
  border: 2px solid black;
  border-bottom: none;
  
}

ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;

  max-width: 100%;
  overflow-x: auto;
  font-size: 1.8rem;
  padding: 1rem;
  border-bottom: 2px solid black;
}

ul li span {
  display: flex;
  align-items: center;
  gap: 3rem;
}

ul li svg {
  font-size: 3rem;
}

/* CONTAINER NEW TASKS FORM */

.container-newTasks {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-newTasks header {
  padding: 1rem;
  font-size: 2rem;
  color: rgb(37, 37, 37);
}

.container-newTasks form {
  margin-block: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.container-newTasks form input {
  width: 80%;
  padding: 1rem;
}

.container-newTasks form button {
  width: 20%;
  padding: 1rem;
  border: none;
  background-color: rgb(17, 17, 17);
  color: white;
  cursor: pointer;
}

.container-newTasks form button:hover {
  background-color: rgba(17, 17, 17, 0.89);
}

/* Grid tarefas */

.grid-tarefas {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  column-gap: 1rem;
}

/* ---------------------------- TAREFAS ABERTAS CONFIGURAÇÃO ------------------ */

.tasksOpen {
  width: 100%;
}

.tasksOpen h2 {
  border: 2px solid black;
  border-bottom: none;

  font-size: 2rem;
  padding: 1rem;
  text-align: center;
  background-color: #FFC700;
}

.tasksOpen ul {
  width: 100%;
  background-color: #FFF384;
}

.msgTarefasFazer {
  font-size: 2rem;
  background-color: white;
  padding: 0.5rem;
  text-align: center;
}

/* ---------------------------- TAREFAS FECHADAS CONFIGURAÇÃO ------------------ */

.tasksDone {
  width: 100%;
}

.tasksDone ul {
  width: 100%;
  background-color: #96E594;
}

.tasksDone h2 {
  border: 2px solid black;
  border-bottom: none;
  
  padding: 1rem;
  font-size: 2rem;
  text-align: center;
  background-color: #77C06B;
}

.mgsTarefasConcluidas {
  font-size: 2rem;
  background-color: white;
  padding: 0.5rem;
  text-align: center;
}